.field-input {
  display: flex;
  justify-content: space-between;
  align-items: center;

  font-size: $font-size-medium;
  text-transform: uppercase;
  color: $black;
}

.field-input__label {
  padding-right: 10px;
  flex-shrink: 0;
}

.field-input__input {
  width: 100%;
  padding: 13px 40px 12px 15px;
  box-sizing: border-box;

  border: 1px solid rgba($grey-lighter, 0.5);
  text-overflow: ellipsis;
  text-transform: uppercase;
  transition: 200ms ease;
  outline: none;
  appearance: textfield;
}

.field-input__input:hover {
  border: 1px solid $grey-lighter;
}

.field-input__input:focus {
  border: 1px solid $main-color;
  box-shadow: inset 0 0 0 1px $main-color;
}

// todo  field-input--error    ,     

.field-input--error .field-input__input:not(:focus):invalid {
  border: 1px solid $pink;
  box-shadow: inset 0 0 0 1px $pink;
}

.field-input__input::placeholder {
  text-transform: uppercase;
  color: $main-color;
}

.field-input__container {
  position: relative;

  flex-grow: 1;
}

.field-input__picture {
  position: absolute;
  top: 50%;
  right: 4%;
  transform: translate(-50%, -50%);

  display: block;
  fill: $black;
}

.field-input__input:focus ~ .field-input__picture {
  fill: $green;
}

.field-input--error:not(:focus):invalid ~ .field-input__picture {
  fill: $pink;
}

@media (min-width: $tablet-width) {
  .field-input {
    font-size: $font-size-larger;
    line-height: 20px;
  }

  .field-input__input {
    padding: 13px 60px 13px 18px;
  }

  .field-input__label {
    padding-right: 15px;
  }

  .field-input__input--mail {
    margin-left: 6px;
  }

  .field-input__picture {
    right: 7px;
  }

  .field-input__input--tel ~ .field-input__picture {
    right: 2px;

    width: 22px;
    height: 20px;
  }
}

@media (min-width: $desktop-width) {
  .field-input__label {
    padding-right: 19px;
  }

  .field-input__input {
    padding: 15px 60px 13px 18px;
  }

  .field-input__input--mail {
    margin-left: 0;
  }

  .field-input__picture {
    right: 13px;
  }

  .field-input__input--tel ~ .field-input__picture {
    right: 8px;
  }
}
